  ngOnInit(): void {
      this.dtOptions = {
        pagingType: 'full_numbers',
        pageLength: 10,
        processing: true
      };

    if(sessionStorage != undefined){
      this.loggedUser = JSON.parse(sessionStorage.loggedUser);

      this.username = this.loggedUser.username;
      this.email = this.loggedUser.email;
      this.phone = this.loggedUser.phone;
      this.lastname = this.loggedUser.lastName;
      this.firstname = this.loggedUser.firstName;
      this.idUser = this.loggedUser.id;
      this.startup.getReserveUser(this.loggedUser.id).toPromise().then(response=>{
        this.myReservations = response;
      });

      _.each(this.loggedUser.announces, (item, index) =>{
        this.startup.getReserveAnn(item.id).toPromise().then(response=>{
          this.loggedUser.announces[index]['reservations'] = response.count;
          if(response.count > 0){
            this.annonceReserve(response);
          }
        });
      });

      // _.each(this.loggedUser.announces, (item) =>{
      //   console.log(item.reservations);
      //   this.countAnnWithRes += item['reservations'];
      // });
      this.annonces = this.loggedUser.announces;
      this.username.toUpperCase();
    }

    this.updateUser = this.formBuilder.group({
       firstName: [this.firstname, Validators.required],
       lastName: [this.lastname, Validators.required],
       // username: ['', Validators.required],
       phone: [this.phone, Validators.required]
       // email: [this.email, [Validators.required, Validators.email]],
       // password: ['', [Validators.required]],
       // password1: ['', [Validators.required]],
       // password2: ['', [Validators.required]],
       },
       // {validator: this.passwordConfirming}
     );

    this.updateUserMDP = this.formBuilder.group({
       password: ['', [Validators.required]],
       password1: ['', [Validators.required]],
       password2: ['', [Validators.required]],
       },
       {validator: this.passwordConfirming}
     );
  }

  passwordConfirming(c: AbstractControl): { invalid: boolean } {
   if (c.get('password1').value !== c.get('password2').value) {

       var element = document.getElementsByName('password2');
       element.forEach(elt => {
         elt.className = elt.className.replace(/\bng-valid\b/g, "displaystyle-warning");
       });

     return {invalid: true};
   }
   else{
     var element = document.getElementsByName('password2');
     element.forEach(elt => {
       elt.className = elt.className.replace(/\bdisplaystyle-warning\b/g, "ng-valid");
     });
   }
 }

toggleView(x){
  if(this.viewTable){
    this.viewTable = false;
    x.innerHTML = "Tableau";
  }else{
    this.viewTable = true;
    x.innerHTML = "Carte";
  }
}
 annonceReserve(response){
   this.annonceWithRes.push(response.results);
   this.countAnnWithRes += response.count;
 }

 valide(id){
   let val={
     "id":id,
     "validate": true
   };
   this.startup.validReserve(val).toPromise().then(response =>{
     if(response.retCode != -1){
       this.notifyService.showSuccess(response.retDescription,"");
       this.refreshPage();
     }else{
       this.notifyService.showError("Une erreur s'est produite durant la validation","");
     }
   });
 }
 refus(id){
   let val={
     "id":id,
     "validate": false
   };
   this.startup.validReserve(val).toPromise().then(response =>{
     if(response.retCode != -1){
       this.notifyService.showSuccess(response.retDescription,"");
       this.refreshPage();
     }else{
       this.notifyService.showError("Une erreur s'est produite durant le refus","");
     }
   });

 }

