  minus(){
    this.annonceRes.announceInfo.remainWeight++;
    this.annonceRes.weight--;
  }

  plus(){
    this.annonceRes.weight++;
    this.annonceRes.announceInfo.remainWeight--;
  }

  editRes(res,bool){
    this.views = bool;
    if(bool){
      this.titleModal = "Detail de la reservation";
    }else{
      this.titleModal = "Modifier la reservation";
    }
    this.annonceRes = res;
    console.log(res);

    this.catRes = res.categories;
    this.noteRes = res.description;
    $("#modalUpdRes").modal("show");
  }

  updateRes(){
    let cat1="", cat2="", cat3="", cat4="", cat5="";
    if($("#Checkbox0u").prop("checked") == true){
      cat1 = $("#Checkbox0u").val();
    }
    if($("#Checkbox1u").prop("checked") == true){
      cat2 = $("#Checkbox1u").val();
    }
    if($("#Checkbox2u").prop("checked") == true){
      cat3 = $("#Checkbox2u").val();
    }
    if($("#Checkbox3u").prop("checked") == true){
      cat4 = $("#Checkbox3u").val();
    }
    if($("#Checkbox4u").prop("checked") == true){
      cat5 = $("#Checkbox4u").val();
    }

    let res ={
      "announceId":	this.annonceRes.announceInfo.id,
      "categories":	[cat1,cat2,cat3,cat4,cat5],
      "description":	this.noteRes,
      "userId":	this.loggedUser.id,
      "weight": this.annonceRes.weight
    };

      this.startup.updateReserve(this.annonceRes.id,res).toPromise().then(response =>{
        if(response.retCode != -1){
          this.myReservations.results = _.filter(this.myReservations.results, item =>{
            return item.id != response.id;
          });
          this.myReservations.results.push(response);
          this.notifyService.showSuccess(response.retDescription,"");
          this.refreshPage();
        }else{
          this.notifyService.showError("Une erreur s'est produite durant la reservation","");
          // this.router.navigate(["/home"]);
        }
      });
  }

  delRes(res){
    this.annonceRes = res;
    $("#modalDeleteRes").modal("show");
  }
  deleteRes(){
    this.startup.deleteReserve(this.annonceRes.id).toPromise().then(response =>{
      if(response.retCode != -1){
        this.myReservations.results = _.filter(this.myReservations.results, item =>{
          return item.id != response.id;
        });
        this.notifyService.showSuccess(response.retDescription,"");
        this.refreshPage();
      }else{
        this.notifyService.showError(response.message,"");
        // this.refreshPage();
      }
    });
  }
